  align-items: flex-start;
}
.hamburger-menu {
  position: fixed;
  top: 0;
  right: 0;
  margin: 20px;
  display: none;
}
.alink:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -4px;
  width: 0;
  height: 2px;
  background-color: #33cc33; /* color of the underline */
  transition: width 0.3s ease-in-out;
}
.alink.active::before {
  width: 50%;
}
.alink:hover:before {
  width: 50%; /* width of the underline */
}
